import uParse from '@/plugins/gaoyia-parse/parse.vue';
import { travelsDetail } from '@/http/api.js';
export default {
	components: {
		uParse
	},
	data() {
		return {
			id:'41',
			list:[]
		}
	},
	onShow() {
		wx.hideHomeButton()
	},
	onLoad(options) {
		if(options.id){
			this.id = options.id;
		}
		this.getDetail(this.id)
	},
	methods: {
		getDetail(id){
			travelsDetail(id).then(res => {
				this.list = res.data;
			});
		},
		toAll(){
			uni.navigateTo({
				url: "/pages/newDetail/comments/comments?id=" + this.id +"&type=travels"
			});
		}
	}
}
